<template>
    <div class="common-wrap">
        <div class="common-left" :style="{width: (100 - otherVariable.contentWidth) + '%'}" v-show="!otherVariable.isSpread">
            <div class="not-allowed-wrap"  v-if="isShowNotAllowed"></div>
            <!---->
            <div class="common-btn-wrap">
                <el-button class="btn-cyan add-btn" @click="handleMulAudit" :disabled="otherVariable.isSaving"></el-button>
                <el-button @click="handleRefresh"></el-button>
            </div>
            <!---->
            <CommonSearch
                :isShowStatus="isShowStatus"
                :propData="searchProp"
                :searchForm="searchForm"
                @getData="getSearchList"
            >
                <template slot="search-other-btn">
                    <el-button @click="handleReset"></el-button>
                </template>
            </CommonSearch>
            <!---->
            <CommonTable
                v-if="isResize"
                ref="commontable"
                v-loading="loadingI"
                :mainHeight="mainHeight"
                :isShowStatus="isShowStatus"
                :propData="tableProp"
                :otherVariable="otherVariable"
                :tableData="tableData"
                @handleDetail="handleDetail"
                @handleSelectionChange="handleSelectionChange"
            >
            </CommonTable>
            <!---->
            <CommonPagination
                :pageObj="pageObj"
                @getData="getData"
            >
            </CommonPagination>
        </div>
        <div class="common-right" :style="{width: otherVariable.contentWidth + '%'}">
            <!--/-->
            <CommonDetailX
                :otherVariable="otherVariable"
                :propData="formProp"
                :detailForm="detailForm"
                @toggleSpread="toggleSpread"
            >
                <!---->
                <template slot="header">
                    <div class="header-form" ref="headForm">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'outter'">
                                <span>{{ item.name ? (item.name + ':') : '' }}</span>
                                <span>{{ detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                </template>
                <!---->
                <template slot="main-btn-wrap">
                    <div  class="xs-audit-icon" @click="handleAudit" ></div>
                    <!--v-if="showUnAudit"-->
                </template>
                <!---->
                <template slot="head-form-title">
                    <div class="head-form-title">
                        <span>&nbsp;{{ detailForm.projectName }}{{ detailForm.projectCode?("("+ detailForm.projectCode + ")"):"" }}</span>
                        <span></span>
                    </div>
                </template>
                <!---->
                <template slot="head-form-content">
                    <div class="head-form-content">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'inner' && !item.isLong">
                                <span>{{ item.name }}</span>
                                <span>{{ detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                    <!--<div class="project-wrap">-->
                        <!--<div></div>-->
                        <!--<div class="scrollBar" :title="detailForm.projectName">-->
                            <!--&nbsp;{{ detailForm.projectName }}{{ detailForm.projectCode?("("+ detailForm.projectCode + ")"):"" }}-->
                        <!--</div>-->
                    <!--</div>-->
                    <div class="remark-wrap scrollBar">
                        <div></div>
                        <div class="headspan" :title="detailForm.remark">{{ detailForm.remark }}</div>
                    </div>
                </template>
                <!---->
                <template slot="common-content">
                    <!--tab-->
                    <el-tabs v-model="activeName" class="common-content-tab order_style">
                        <el-tab-pane label="" name="first">
                            <CommonTable
                                v-if="isResize"
                                v-loading="loadingI"
                                ref="childTable"
                                :isSum="true"
                                :sumParam = {digit:12,theoryWght:15}
                                :isCenter="true"
                                :isMulTable="true"
                                :isNotMulSelect="false"
                                :otherVariable="otherVariable"
                                :formTableData="tabDataI"
                                :formPropTable="tabPropI"
                                :tableHeight="tableHeight"
                            >
                            </CommonTable>
                        </el-tab-pane>
                    </el-tabs>
                </template>
            </CommonDetailX>
        </div>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                tableHeight: this.$store.state.screenHei*0.60,
                mainHeight: 716,
                isShowNotAllowed: false,
                loadingI:false,
                hanleNo:'',
                isShowStatus: {
                    isMore: false,
                    isRead: true
                },
                isResize: true,

                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    popupTitle: '',
                    isMulSelect: true,
                    isInlineForm: true,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    isReadTable: true,
                    isSaving:false,
                },
                tempWidth: null,
                tableData: [],
                showButton: false,
                showUnAudit: false,
                //
                searchProp: [
                    {
                        name:'',
                        prop:'docCode',
                        type:'normal',
                        isMainSearch:true,
                        isMain:false,
                        placeholder:''
                    },
                    {
                        name:'',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },

                    {
                        name:'',
                        prop:'clientName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },

                    {
                        name:'',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                //
                formProp: [
                    {name:'',prop:'refDocCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'docStatusName',type:'outter'},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'clientCode2',type:'inner'},
                    {name:'',prop:'weighman',type:'inner'},
                    {name:'',prop:'theoryWght',type:'inner'},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'projectCode',type:'inner'},
                    {name:'',prop:'stockman',type:'inner'},
                    {name:'',prop:'actWght',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //
                tableProp: [
                    {
                        name1:'/',
                        prop1:'docCode',
                        name2:'',
                        prop2:'docStatusName',
                        status: 'docStatus',
                        type:'mixinStatus',
                        hasCircleI: true,
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'refDocId',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '120'
                    },
                    {
                        name1:'/',
                        prop1:'clientCode',
                        name2:'',
                        prop2:'clientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'storageCode',
                        name2:'',
                        prop2:'theoryWght',
                        type:'mixin',
                        width: '138'
                    }
                ],
                searchForm: {},
                detailForm: {},
                copyForm: {},
                detailId: null,
                parentId: null,
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                //
                tabLeft: '200px',
                activeName: 'first',
                //
                tabPropI: [
                    {prop:'docItem', name:'',type:'normal',isShort: true,width:'40',align:'center'},
                    {
                        prop:'',name:'',
                        children: [
                            {prop:'matCode',name:'',width: 55, align:'center',type:'normal'},
                            {prop:'matName',name:'',width: 55, align:'center',type:'normal'}
                        ]
                    },
                    {
                        prop:'',name:'',
                        children: [
                            {prop:'cx1Code',name:'',width: 55, align:'center',type:'normal'},
                            {prop:'cx1Name',name:'',width: 55, align:'center',type:'normal'}
                        ]
                    },
                    {prop:'cx2Name',name:'',type:'normal'},
                    {prop:'cx4',name:'',type:'normal',rule:'limitLength',unit:' m',isShort: true,align:'right'},
                    {prop:'cx3',name:'',type:'normal',unit:'',isShort: true,align:'right'},
                    {prop:'digit',name:'',type:'normal',isShort: true,align:'right'},
                    {prop:'meterWght',name:'',type:'normal',rule:'limitLength',unit:' kg/m',isShort: true,align:'right'},
                    {prop:'unitWght',name:'',type:'normal',unit:' kg/pcs',isShort: true,align:'right'},
                    {prop:'theoryWght',name:'',type:'normal',rule:'limitLength',unit:' kg',isShort: true,align:'right'},
                    {prop:'unitSqm', name:'',type:'normal'},
                    {prop:'remark',name:'',type:'normal',width:200}
                ],
                //
                tabDataI: [],
                copyDataI: [],
                //
                selectOrderList: []
            }
        },
        mounted() {
            this.getData();
            // /
            this.listenScroll();
            this.listenResize();
            this.changeSize();
            this.tempWidth = this.otherVariable.contentWidth;
        },
        methods: {
            //
            getData() {
                this.tableData = [];
                this.fetch(`/oms/wms/WmsStockoutMasterController/getStockoutMasterList?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            this.$nextTick(() => {
                                this.$refs.commontable.renderRow(this.tableData[0].fid);
                            })
                        }else {
                            this.tabDataI = [];
                            this.copyDataI = [];
                            this.detailForm = {};
                            this.detailId = null;
                            this.parentId = null;
                            this.pageObj.commonTotal = 0;
                            this.$nextTick(() => {
                                this.$refs['childTable'].initStatus();
                            })
                        }
                    })
            },

            //
            handleDetail(row) {
                this.tabDataI = [];
                this.copyDataI = [];
                this.isShowStatus.isRead = true;
                this.detailForm = {};
                this.fetch('/oms/wms/WmsStockoutMasterController/getStockoutMasterList',{fid:row.fid})
                    .then(result => {
                        if(result.status === 200) {
                            this.detailForm = result.data.list[0];
                            this.copyForm = this.$lodash.cloneDeep(result.data.list[0]);
                        }else {
                            this.$message({
                                message: '',
                                type: 'warning'
                            });
                            this.detailForm = {};
                            this.copyForm = {};
                        }
                    });
                this.getAjax(`/oms/wms/WmsStockoutDetailController/getStockoutDetailListByMaster?masterId=${row.fid}`)
                    .then(result => {
                        this.loadingI = false;
                        this.isShowNotAllowed = false;
                        if(result.data && result.data.list) {
                            this.tabDataI = result.data.list;
                            this.copyDataI = this.$lodash.cloneDeep(this.tabDataI);
                        }
                    })
                    .then(() => {
                        this.loadingI = false;
                        this.$refs['childTable'].initStatus();
                    });
                this.detailId = row.fid;
                this.parentId = row.parentId;
            },
            handleSelectionChange(rows) {
                this.selectOrderList = [];
                rows.forEach(item => {
                    this.selectOrderList.push(item.fid)
                })
            },
            //
            handleMulAudit() {
                if(this.selectOrderList.length) {
                    this.$confirm(''+this.selectOrderList.length+'?','',{
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(()=>{
                        this.fetch('/oms/sls/SalesOrderMaster/batchAuditOrder',this.selectOrderList)
                            .then(result => {
                                if(result.status ===200){
                                    this.$message({
                                        message:result.message,
                                        type:'success'
                                    });
                                    this.getData();
                                }else{
                                    this.$message({
                                        message:this.returnErrorMsg(result.error),
                                        type:'error'
                                    })
                                }
                            })
                    })

                }else {
                    this.$message({
                        message: '',
                        type:'warning'
                    })
                }
            },
            //
            handleRefresh() {
                this.getData();
                this.detailForm = {};
                this.detailId = null;
                this.parentId = null;
                this.tabDataI = [];
                this.copyDataI = [];
            },
            //
            handleReset() {
                this.searchForm = {tempStatus: 2,minStatus:50,minStatusOperator:'>=',maxStatus:100,maxStatusOperator:'<'};
                this.handleRefresh();
            },
            //
            changeRefresh() {
                this.detailForm = {};
                this.copyForm = {};
                this.tabDataI = [];
                this.copyDataI = [];
                this.getData();
                this.otherVariable.isChildEdit = false;
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData();
            },
            //
            handleAudit() {
                if(this.detailForm.fid) {
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.otherVariable.isSaving = true;
                        this.getAjax(`/oms/sls/SalesOrderMaster/auditOrder?orderId=${this.detailForm.fid}&docStatus=${this.detailForm.docStatus}`)
                            .then(result => {
                                if(result.status === 200) {
                                    this.$message({
                                        message:result.message,
                                        type:'success'
                                    });
                                    this.getData();
                                }else {
                                    this.$message({
                                        message:this.returnErrorMsg(result.error,'second'),
                                        type:'error'
                                    })
                                }
                            })
                            .then(() => {
                                this.otherVariable.isSaving = false;
                            })
                    }).catch(() => {
                        console.log('cnacel')
                    })

                }else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }
            },
            //
            handleUnAudit() {
                this.getAjax(`/oms/sls/SalesOrderMaster/unAuditOrder?orderId=${this.detailForm.fid}&docStatus=${this.detailForm.docStatus}`)
                    .then(result => {
                        if(result.status ===200){
                            this.$message({
                                message:result.message,
                                type:'success'
                            });
                            this.getData();
                        }else{
                            this.$message({
                                message:this.returnErrorMsg(result.error,'second'),
                                type:'error'
                            })
                        }
                    });
            },
            // 
            handlePrint() {
                let isReturn = false;
                let resultUrl = '';
                this.getAjax(`/oms/PrintController/isPrint?fId=${this.detailId}`)
                    .then(result => {
                        if(result.status === 200) {
                            isReturn = true;
                            resultUrl = result.data;
                        }else {
                            isReturn = false;
                            this.$message({
                                type:'error',
                                message: result.error || ''
                            })
                        }
                    })
                    .then(() => {
                        if(isReturn) {
                            window.open(resultUrl);
                        }
                    })
            },
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
</style>



